import React, { Component } from 'react';
import '../App.css';
import '../index.css';
import constants from '../common/constant.js'

import Header from '../common/header.js'
import { Link } from "react-router-dom";
import Web3 from 'web3';


import _ from 'lodash'
import hmac from 'js-crypto-hmac';

class Dashboard extends Component {
  constructor(props){
    super(props)
    this.state = {
        //0: customer ledger, 1: merchant ledger
        display: 0,
        tob_bid: 'Loading',
        tob_ask: 'Loading',
        ask_price: [],
        ask_size: [],
        bid_price: [],
        bid_size: []
    }
  }


componentDidMount(){
  
  fetch("https://api.gemini.com/v1/book/ethusd?limit_bids=10&limit_asks=10")
  .then((response) => response.json())
  .then((data) => {

    let ask_price = []
    let ask_size = []
    for (let i = 0; i < data['asks'].length; i++){
      ask_price.push(data['asks'][i]['price'])
      ask_size.push(data['asks'][i]['amount'])
    }

    let bid_price = []
    let bid_size = []
    for (let i = 0; i < data['bids'].length; i++){
      bid_price.push(data['bids'][i]['price'])
      bid_size.push(data['bids'][i]['amount'])
    }

    this.setState({
      tob_ask: data['asks'][0]['price'],
      tob_bid: data['bids'][0]['price'],
      ask_price: ask_price,
      ask_size: ask_size,
      bid_price: bid_price,
      bid_size: bid_size
    })
    document.getElementById("price").value = data['bids'][0]['price']
  })
 
}


  render() {
  
    return (
      <div>
      <Header/>
      <section className="section">

      <div className="container" style={{width: 1500}}>
      <div className="columns">
      <div className="column is-one-third">
        
      <ETHUSD 
      balance={this.state.balance}
      bid={this.state.tob_bid} 
      ask={this.state.tob_ask}
      />
      <div className="box">
      <div className="field">
          <label className="label has-text-left">Api Key</label>
          <div className="control has-icons-left has-icons-right">
              <input className="input" type="password" id='key'/>
              <span className="icon is-small is-left">
              <i className="fas fa-key"></i>
              </span>
          </div>
          <br/>
          </div>

          <div className="field">
          <label className="label has-text-left">Api Secret</label>
          <div className="control has-icons-left has-icons-right">
              <input className="input" type="password" id='secret'/>
              <span className="icon is-small is-left">
              <i className="fas fa-lock"></i>
              </span>
          </div>
          <br/>
          </div>
        </div>
      
      </div>
        
        <div className="column">
        <div className="box">
       
        <Table
        tob_ask={this.state.tob_ask}
        tob_bid={this.state.tob_bid}
        ask_price={this.state.ask_price}
        ask_size={this.state.ask_size}
        bid_price={this.state.bid_price}
        bid_size={this.state.bid_size}
        />
        <br/>
        
        </div>
        </div>
        </div>
        </div>
        
        </section>
        </div>
        
    );
  
}
}



class BTCUSD extends Component {

  
  render() {
    return (
      <div className="box">
        <p className="is-size-4 has-text-weight-semibold">BTC/USD</p>
        
        <p className="is-size-3"><a className="has-text-dark" href="#" title={this.props.balance}>
          ${+(this.props.balance).toFixed(10)}</a> USD</p>
        
        </div>

    );
  }
}

class ETHUSD extends Component {
  constructor(props){
    super(props)
    this.state = {
        //0: customer ledger, 1: merchant ledger
        total: '--'
    }
  }


  checkAmountInputText(){
    const input = document.getElementById("price").value
    if (String(parseFloat(input)) === input && String(parseFloat(input)) > 0){
        return true
    }
    else {

        return false
    } 
  }

  onPriceChange(){
    document.getElementById("total").value = Math.trunc(100*(parseFloat(document.getElementById("qty").value) * parseFloat(document.getElementById("price").value)))/100
    this.setState({
      total: parseFloat(document.getElementById("total").value)
    })  
  }

  onQtyChange(){
    document.getElementById("total").value = Math.trunc(100*(parseFloat(document.getElementById("qty").value) * parseFloat(document.getElementById("price").value)))/100
    this.setState({
      total: parseFloat(document.getElementById("total").value)
    })  
  }

  onTotalChange(){
    document.getElementById("qty").value = Math.trunc(100*parseFloat(document.getElementById("total").value)/parseFloat(document.getElementById("price").value))
    this.setState({
      total: parseFloat(document.getElementById("total").value)
    })   
  }

  onOrderSubmission(){
    const base_url = 'https://cors-anywhere.herokuapp.com/' + "https://api.sandbox.gemini.com"
    const endpoint = "/v1/order/new"
    const url = base_url + endpoint

    const amount = parseFloat(document.getElementById("qty").value)
    const price = parseFloat(document.getElementById("price").value)

    const key = document.getElementById("key").value
    const secret = btoa(document.getElementById("secret").value)
   
    var payload_nonce = (new Date).getTime();

    let payload = {
      "request": endpoint,
       "nonce": payload_nonce,
       "symbol": "ethusd",
       "amount": amount,
       "price": price,
       "side": "buy",
       "type": "exchange limit",
       "options": ["maker-or-cancel"] 
   }
   const encoded_payload = JSON.stringify(payload)
   const b64 = btoa(encoded_payload)

   const msg = b64 // Uint8Array of arbitrary length
   const hash = 'SHA-384';

   hmac.compute(secret, msg, hash).then( (signature) => {
    // now you get a keyed-hash of msg in Uint8Array
    const request_headers = { 
    'Content-Type': "text/plain",
    'Content-Length': "0",
    'X-GEMINI-APIKEY': key,//gemini_api_key,
    'X-GEMINI-PAYLOAD': b64,
    'X-GEMINI-SIGNATURE': signature,
    'Cache-Control': "no-cache"
    //'Access-Control-Allow-Origin':'*' 
  }

    fetch(url, {
      method: 'POST', // or 'PUT'
      headers: request_headers
    }).then(res => res.json())
    .then(response => console.log('Success:', JSON.stringify(response)))
    .catch(error => console.error('Error:', error));



  })








  }

    render() {
      return (
        <div className="box">
          <p className="is-size-4 has-text-weight-semibold">Buy ETH/USD</p>
          <br/>

          <div className="field">
          <label className="label has-text-left">Order Type</label>
          <div className="control">
            <div className="select is-fullwidth">
              <select>
                <option>Limit</option>
              </select>
            </div>
          </div>
        </div>
        <br/>

          <div className="field">
          <label className="label has-text-left">Price</label>
          <div className="control has-icons-left has-icons-right">
              <input className="input" type="text" id='price'
              onChange={() => 
              this.onPriceChange()}/>
              <span className="icon is-small is-left">
              <i className="fas fa-dollar-sign"></i>
              </span>
          </div>
          <br/>
          </div>

          <div className="field">
          <label className="label has-text-left">Quantity</label>
          <div className="control has-icons-left has-icons-right">
              <input className="input" type="text" id='qty' 
              onChange={() => 
              this.onQtyChange()}/>
              <span className="icon is-small is-left">
              <i className="fab fa-ethereum"></i>
              </span>
          </div>
          <br/>
          </div>

          <div className="field">
          <label className="label has-text-left">Total</label>
          <div className="control has-icons-left has-icons-right">
              <input className="input" type="text" id='total'
              onChange={() =>
              this.onTotalChange()}/>
              <span className="icon is-small is-left">
              <i className="fas fa-dollar-sign"></i>
              </span>
          </div>
          <br/>
          </div>

          <table className="is-fullwidth table">
          <tr className="is-size-7">
            <td>Order</td>
            <td className="has-text-right">{this.state.total == '--' ? '--' : `$${this.state.total}`}</td>
          </tr>
          <tr className="is-size-7">
            <td>Fees</td>
            <td className="has-text-right">{this.state.total == '--'? '--' : `$${Math.trunc(this.state.total)/1000}`}</td>
          </tr>
          <tr className="is-size-7">
            <td>Total</td>
            <td className="has-text-right">{this.state.total == '--'? '--' : `$${Math.trunc(this.state.total)/1000 + this.state.total}`}</td>
          </tr>
          </table>

          <button className="button is-fullwidth is-medium is-primary" onClick={() => this.onOrderSubmission()}>Buy</button>
          </div>
  
      );
    }
  }

class Table extends Component {
  constructor(props){
    super(props)
    this.state = {
    }
  }
  

  componentDidMount(){
  }

  

  render() {

  let table = []
  for (let i = 0; i < this.props.ask_price.length; i++){
    table.push(
      <tr key={`${this.props.bid_price[i]}${this.props.ask_price[i]}`}>
        <td>{this.props.bid_size[i]}</td>
        <td>{this.props.bid_price[i]}</td>
        <td>{this.props.ask_price[i]}</td>
        <td>{this.props.ask_size[i]}</td>
      </tr>
    )
  }

  
    return (
      <div>
 
      <div>
      <nav className="level">
      <div className="level-left">
        <div className="level-item">
        <p className="is-size-5">ETH/USD Orderbook
        </p>
        
        </div>
        </div>
        <div className="level-right">
        <div className="level-item">
        <p className="is-size-7">
        Top Bid: {this.props.tob_bid}<br/>
        Top Ask: {this.props.tob_ask}<br/>
        Showing 10 levels
        </p>
        </div>
        </div>
        </nav>
        <table className="table is-fullwidth">
          <thead>
          <th>Bid ETH Amount</th>
          <th>Bid Price</th>
          <th>Ask Price</th>
          <th>Ask ETH Amount</th>
          </thead>
          <tbody>
        {table}
          </tbody>
        </table>


      

        
        </div>


        <br/>

        <p className="is-size-7">Data from: <a href="https://api.gemini.com/v1/book/ethusd" target="_blank" className="is-size-7">https://api.gemini.com/v1/book/ethusd</a>
        </p>

</div>
        
    );
  }
}

export default Dashboard;